<template>
  <div>
    <NavBarVue>
      <template #title>
        <div>Ë¥≠Áâ©ËΩ¶</div>
      </template>
    </NavBarVue>
    <section v-if="shopCart.length>0">
      <div class="shopCart">
        <section class="cart-box" v-for="item in shopCart" :key="item.goodsId">
          <van-swipe-cell id="swipe">
            <van-checkbox v-model="item.done"></van-checkbox>
            <van-card
              :num="item.goodsCount"
              :price="item.sellingPrice"
              :title="item.goodsName"
              :thumb="item.goodsCoverImg"
            >
              <template #footer>
                <van-stepper
                  v-model="item.goodsCount"
                  @plus="plus(item)"
                  @minus="minus(item)"
                />
              </template>
            </van-card>
            <template #right>
              <van-button
                square
                text="Âà†Èô§"
                type="danger"
                class="delete-button"
                @click="Del(item.cartItemId)"
              />
            </template>
          </van-swipe-cell>
        </section>
      </div>
      <van-submit-bar
        :price="allPrice"
        button-text="ÁªìÁÆó"
        @submit="onSubmit"
        :disabled="allPrice > 0 ? false : true"
      >
        <van-checkbox v-model="allDone">ÂÖ®ÈÄâ</van-checkbox>
      </van-submit-bar>
    </section>
    <div class="isShow-sky" v-else>
      <span>ü§∑</span>
      <p>Ë¥≠Áâ©ËΩ¶Á©∫Á©∫Â¶Ç‰πü</p>
      <div @click="$router.push('/home')">ÂâçÂæÄÈ¶ñÈ°µ</div>
    </div>
  </div>
</template>

<script>
import NavBarVue from "@/components/NavBar.vue";
import { changeGoodsCount, delGoods } from "../../api/index.js";
export default {
  name: "cart",
  components: {
    NavBarVue,
  },
  data() {
    return {
      value: 1,
      cartItemIds: [], //ÊâÄÂãæÈÄâÁöÑÂïÜÂìÅidÊï∞ÊçÆ
    };
  },
  computed: {
    //Ë¥≠Áâ©ËΩ¶Êï∞ÊçÆ
    shopCart() {
      return this.$store.state.shopCart;
    },
    // ÂÖ®ÈÄâÂèçÈÄâ
    allDone: {
      get() {
        return this.shopCart.every((item) => item.done);
      },
      set(val) {
        console.log(val);
        this.shopCart.forEach((item) => (item.done = val));
      },
    },
    // ÂïÜÂìÅÊÄª‰ª∑
    allPrice() {
      return this.shopCart.reduce((a, b) => {
        return a + (b.done == true ? b.goodsCount * b.sellingPrice * 100 : 0);
      }, 0);
    },
  },
  methods: {
    //Ê†πÊçÆcartItemIdÂà†Èô§ÂïÜÂìÅ
    Del(id) {
      delGoods(id).then((res) => {
        if (res.resultCode === 200) {
          this.$toast("Âà†Èô§ÊàêÂäü");
          //Âà∑Êñ∞È°µÈù¢ÔºåÈáçÊñ∞Ëé∑ÂèñË¥≠Áâ©ËΩ¶Êï∞ÊçÆ
          this.$store.dispatch("shopCartAsync");
        }
      });
    },
    // ÁªìÁÆóÊåâÈíÆ
    onSubmit() {
      // ËøáÊª§ÊâÄÂãæÈÄâÂïÜÂìÅÁöÑid
      this.$store.state.shopCart.filter((item) => {
        if (item.done) {
          this.cartItemIds.push(item.cartItemId);
        }
      });
      // ÊåÅ‰πÖÂåñÂ≠òÂÇ®ÂïÜÂìÅid
      localStorage.setItem("cartItemIds", this.cartItemIds);
      // ÂãæÈÄâÁöÑÂïÜÂìÅÊÄª‰ª∑
      localStorage.setItem("allPrice", this.allPrice);
      this.$router.push("/addressList");
    },
    //Â¢ûÂä†ÂïÜÂìÅÊï∞Èáè
    plus(item) {
      changeGoodsCount({
        cartItemId: item.cartItemId,
        goodsCount: item.goodsCount + 1,
      }).then();
    },
    // ÂáèÂ∞ëÂïÜÂìÅÊï∞Èáè
    minus(item) {
      changeGoodsCount({
        cartItemId: item.cartItemId,
        goodsCount: item.goodsCount - 1,
      }).then();
    },
  },
  created() {
    //Ëé∑ÂèñË¥≠Áâ©ËΩ¶Êï∞ÊçÆ
    this.$store.dispatch("shopCartAsync");
  },
};
</script>

<style scoped lang="less">
.isShow-sky{
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  div{
    text-align: center;
    padding: 10px 30px;
    background: #1baeae;
    color: #fff;
    font-size: 14px;
  }
  p{
    margin: 10px;
    font-size: 18px;
  }
}
/deep/.van-swipe-cell__wrapper {
  align-items: center;
  width: 100%;
  display: flex;
}
.van-submit-bar {
  position: fixed;
  bottom: 1.3rem;
}
.shopCart {
  margin-bottom: 0.6rem;
  .cart-box {
    padding: 0 0.15rem;
    .div#swipe {
      width: 100%;
    }
    .van-card {
      width: 100%;
      margin-left: 0.05rem;
      .van-card__price {
        color: red;
      }
    }
  }
}
.goods-card {
  margin: 0;
  background-color: white;
}

.delete-button {
  height: 100%;
}
</style>